<template>
	<div>
		<div class="Details-div clearfix">
			<div class="Details-div-size">
				<span>新品</span>{{lisize.text}}
			</div>
			<div style="margin: 0.2em 0;">
				<div>
					<span style="font-size: 18px;color: #ff0000;" class="Details-div-size-span">{{lisize.digital}}</span>
				</div>
			</div>
			<span class="Detailsleft">降价提醒</span><span class="Detailsright">销量{{lisize.Cross}}件</span>
		</div>
		<div class="Details-div2" @touchend="faangleup">
			<div class="Details-div2-div1">促销</div>
			<div style="margin-right: 0.3em;margin-bottom: 0.5em;">
				<div style="display: flex;">
					<span class="Details-div2-div1-span1">包邮</span>
					<span class="Details-div2-div1-span2">全场满99包邮</span>
				</div>
			</div>
			<div style="font-size: 2em;color: #BABABA;height: 1em;position: absolute;right: 0;line-height: 1em;" >
				<i class="fa fa-angle-up faangleup"></i>
			</div>
		</div>
		<div class="Details-color" >
			<div class="Details-color-div" @touchend="faangleup2">请选择尺码颜色</div>
			<div style="font-size: 1.7em;color: #BABABA;height: 1em;position: absolute;right: 0;line-height: 1em;top: 0.14rem;" >
				<i class="fa fa-angle-right faangleup2" @touchend="faangleup2"></i>
			</div>
		</div>
		<div class="Details-Store">
			<span style="display: block;width: 100%;">查看附近有售门店</span>
			<div style="font-size: 1.7em; color: rgb(186, 186, 186); height: 1em; position: absolute; right: 0px; line-height: 1em; top: 0.14rem;">
			<i class="fa fa-angle-right faangleup3"></i>
			</div>
		</div>
		<div class="Details-Store-div">
			<div><img src="../../assets/images/yes.jpg"/></div>正品保证
			<div><img src="../../assets/images/yes.jpg"/></div>15天无理由退货
			<div><img src="../../assets/images/yes.jpg"/></div>发货说明
			<div><img src="../../assets/images/yes.jpg"/></div>退货说明
		</div>
		<div style="margin-top: 0.4em;position: relative;">
			<div class="Details-evaluation-div1">商品评价(4)</div>
			<div class="Details-evaluation-div2">
				<div style="    border-bottom: 1px solid #f1f1f1">
					******匿名
				</div>
				<div style="color: #B4B4B4;padding-left: 1.9em;margin-top: 0em;padding-bottom: .3rem;border-bottom: 1px solid #f3f3f3;">
					经常购买，现在裤子没有喜欢的颜色啦。
				</div>
				<div style="    padding-bottom: 1.8em;border-bottom: 1px solid #f3f3f3;">
					浅蓝 M <span style="float: right;color: #b1b1b1;">2017-07-03 10:45:48</span>
				</div>
			</div>
			<div class="Details-view">
				<div style="margin: 0 auto;text-align: center;width: 1.9rem;" @touchend="Detailsview">查看更多评价</div>
			</div>
		</div>
		<div>
			<img src="//img.sanfu.com/img/index_boy/dis_1.jpg"/>
		</div>
		<div style="width: 100%;margin-top: 0.3em;display: flex;">
			<div style="width: 50%;"><router-link to="/"><img src="//img.sanfu.com/img/index_boy/dis_men.png"/></router-link></div>
			<div style="width: 50%;"><router-link to="/"><img src="//img.sanfu.com/img/index_boy/dis_women.png"/></router-link></div>
		</div>
		
		<div class="Details-position">
			<div class="Details-position-div">
				<div class="clearfix">
					<div class="Details-position-div-div1"><img :src="lisize2.imgUrl" class="Detailsimg"/></div>
					<div class="Details-position-div-div2">
						<div style="    display: block;float: left;color: #aaa;margin: 0.5em 0 0 0.5em;">库存: <span class="Details-div-span">{{lisize2.goods}}</span></div>
					</div>
					<div class="Details-position-div-div3" @touchend="Detailsout"><img src="../../assets/images/none.jpg"/></div>
				</div>
				<div style="padding: 0.1em 1.6em;overflow-y: scroll;height: 75%;    color: #474747;">
					<div class="Details-position-div-color">颜色</div>
					<div class="Details-position-div-color-color">
						<div @touchend="jscolor1" class="Details-position-div-color-color1">{{lisize3.color}}</div>
						<div @touchend="jscolor2" class="Details-position-div-color-color2">{{lisize2.color}}</div>
					</div>
					<div style="margin: 0.5em 0;">
						<div class="Details-div-divsize">尺码</div>
					</div>
					<div class="Details-lei">
						<div class="Details-cloth"></div>
						<div class="Details-div-divsize-daxiao" v-for="item in lisize3.sizes" @touchend="changeCity(item)":class="{Detailspositiondivcolorcolorbackground: activeCity == item}">{{item.size}}</div>
						<div class="Details-div-divsize-daxiao2" v-for="item in lisize2.sizes" @touchend="changeCity(item)":class="{Detailspositiondivcolorcolorbackground: activeCity == item}">{{item.size}}</div>
					</div>
					<div style="margin: 0.5em 0;">
						<div style="width: 100%;border-top: 1px dotted #afafaf;padding-top: 1em;padding-bottom: 0.5em;font-size: 16px;">
							<div class="Details-position-div-number1" @touchend="jsnumber1">-</div>
							<div class="Details-position-div-number2">{{number2}}</div>
							<div class="Details-position-div-number3" @touchend="jsnumber2">+</div>
						</div>
					</div>
				</div>
				<div @touchend="determine">
					<div class="Details-determine">
						确认选择
					</div>
				</div>
			</div>			
		</div>
	</div>
</template>

<script lang="babel">
	import bus from "../../assets/js/bus.js";
	import bus2 from "../../assets/js/bus2.js";
	
	export default {
		props: { //从父得到数据
			lisize: {},
			lisize2: {},
			lisize3: {}			
		},
		data() { //数据
			return {
				bool: false,
				showHideBool:false,
				loadHideBool:false,
				activeCity: '',
				number2:1,
				bool2:false	,
				num:1,
				obj:[]
			}
		},
		methods: { //js
			faangleup() {
				if(this.bool) {
					$(".faangleup").removeClass("fa-angle-down");
					$(".faangleup").addClass("fa-angle-up");
					$(".Details-div2-div1-span2").css({ display: "block" });
					this.bool = false;
				} else {
					$(".faangleup").removeClass("fa-angle-up");
					$(".faangleup").addClass("fa-angle-down");
					$(".Details-div2-div1-span2").css({ display: "none" });
					this.bool = true;
				}
			},
			faangleup2() {
				$(".Details-position-div").removeClass("listout");
				$(".Details-position").css({display:"block"});
				$(".Details-position-div").toggleClass("listyes");
				
			},
			jscolor1(){
				$(".Details-position-div-color-color div").removeClass("Detailspositiondivcolorcolorbackground");
				$(".Details-position-div-color-color1").addClass("Detailspositiondivcolorcolorbackground");
				$(".Details-div-span").text(this.lisize3.goodsnbe);
				$(".Details-cloth").css({display:"none"});
				$(".Detailsimg")[0].src=this.lisize2.imgUrl;
				$(".Details-div-divsize-daxiao2").css({display:"none"});
				$(".Details-div-divsize-daxiao").css({display:"block"});
			},
			jscolor2(){
				$(".Details-position-div-color-color div").removeClass("Detailspositiondivcolorcolorbackground");
				$(".Details-position-div-color-color2").addClass("Detailspositiondivcolorcolorbackground");
				$(".Details-div-span").text(this.lisize2.goodsnbe);
				$(".Details-cloth").css({display:"none"});
				$(".Detailsimg")[0].src=this.lisize3.imgUrl;
				$(".Details-div-divsize-daxiao").css({display:"none"});
				$(".Details-div-divsize-daxiao2").css({display:"block"});
			},
			Detailsview(){
				$(".Detailshead-head div").removeClass("Detailsheadbordercolor");
				$(".Detailshead-div3").addClass("Detailsheadbordercolor");
				$(".Details-div3").css({display:"block"});
				$(".Details-div1").css({display:"none"});
				$(".Details-div2").css({display:"none"});
			},
			changeCity(item) {
		      this.activeCity = item;
		      $(".Details-div-span").text(item.number);
		   	},
		   	jsnumber1(){
		   		if(this.number2>1){
		   			this.number2--;
		   		}
		   	},
		   	jsnumber2(){
		   		if(this.number2<999){
		   			this.number2++;
		   		}
		   	},
		   	determine(){
		   		var _this=this;
		   		if($(".Details-lei div").is(".Detailspositiondivcolorcolorbackground")==false){
		   			alert("请输入尺码");
		   		}
		   		if($(".Details-position-div-color-color div").is(".Detailspositiondivcolorcolorbackground")==false){//is判断是否有这个类
		   			alert("请输入颜色");
		   		}
		   		if($(".Details-lei div").is(".Detailspositiondivcolorcolorbackground")==true && $(".Details-position-div-color-color div").is(".Detailspositiondivcolorcolorbackground")==true){
		   			$(".Details-color-div").text("已购:"+$(".Detailspositiondivcolorcolorbackground").text());
		   			$(".Details-position-div").toggleClass("listout");
		   			$(".Details-position-div").removeClass("listyes");
		   			setTimeout(function a(){$(".Details-position").css({display:"none"})},500);	
		   		}
		   		if(this.bool2==true){//购物按键按后提交数据
		   			if($(".Details-lei div").is(".Detailspositiondivcolorcolorbackground")==true){	   				
		   				var jsons={"text":$(".Detailspositiondivcolorcolorbackground").text(),		   				
		   				"img":$(".Detailsimg")[0].src,"name":$(".Details-div-size").text(),
		   				"money":$(".Details-div-size-span").text(),
		   				"size":this.num};
		   				
		   				this.obj.push(jsons)
		   				console.log(this.obj)
		   				bus.$emit("bool2",this.obj);
		   				_this.num++;
		   			}
				}
		   		else{
		   			alert("出错啦请重试")
		   		}
		   	},
		   	Detailsout(){
		   		$(".Details-position-div").toggleClass("listout");
		   		$(".Details-position-div").removeClass("listyes");
		   		setTimeout(function a(){$(".Details-position").css({display:"none"})},500)

		   	}
		},
		components: { //注册模块
			
		},
		updated() {
			var _this=this;			
			bus.$on("bool",function(msg){//得到购物组件bool
				_this.bool2=msg;
			})
			
		},
		mounted() { //得到数据的ajax
			var _this=this;			
			bus.$on("bool",function(msg){//得到购物组件bool
				_this.bool2=msg;
			})
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.Details-div {
		font-size: 1.1em;
		padding: 0.3em 0.5em;
		background: #fff;
		position: relative;
		color: #474747;
	}
	
	.Details-div-size {
		font-size: 16px;
		position: relative;
		& span {
			font-size: 0.8em;
			border-radius: 0.6em;
			background: #ff0000;
			color: #fff;
			padding: 0 0.5em;
			margin-right: 0.5em;
		}
	}
	
	.Detailsleft {
		color: rgb(175, 174, 174);
		border-radius: 0.3em;
		padding: 0px 0.5em;
		float: left;
		margin-top: 0em;
		margin-right: 0.2em;
		font-size: 0.75em;
		border: 1px solid rgb(175, 174, 174);
	}
	
	.Detailsright {
		color: red;
		background: #fff;
		border: 1px solid red;
		border-radius: 0.3em;
		padding: 0.08em 0.5em;
		float: right;
		margin-top: 0em;
		margin-right: 0.2em;
		font-size: 0.75em;
	}
	
	.Details-div2 {
		padding: 0.8em .1rem;
		margin-top: 0.5em;
		display: flex;
		background: #fff;
		font-size: 14px;
	}
	
	.Details-div2-div1 {
		margin-right: 0.5em;
		font-size: 0.9em;
		margin-top: 0.3em;
	}
	
	.Details-div2-div1-span1 {
		display: inline-block;
		padding: 0em 0.5em;
		font-size: 0.6em;
		background: #fff;
		color: #ff0000;
		border: 1px solid #ff0000;
		border-radius: 0.3em;
	}
	
	.Details-div2-div1-span2 {
		font-size: 0.9em;
		color: #797979;
	}
	
	.Details-color {
		margin-top: 0.5em;
		padding: 0.8em 0.5em;
		background: #fff;
		font-size: 0.9em;
		position: relative;
	}
	
	.Details-position {
		position: absolute;
		z-index: 10;
		left: 0px;
		top: 0px;
		width: 100%;
		height: 18964px;
		display: block;
		background: rgba(0, 0, 0, 0.6);
		display: none;
	}
	
	.Details-position-div {
		background: #fff;
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 75%;
		transform: translateY(8.5rem);
	}
	
	.Details-position-div-div1 {
		float: left;
		width: 4em;
		margin: 10px;
	}
	
	.Details-position-div-div2 {
		padding-left: 1.5em;
		padding-top: 0em;
		float: left;
		margin: 10px;
		font-size: 0.8em;
		color: #949393;
	}
	
	.Details-position-div-div3 {
		width: 0.35rem;
		height: .35rem;
		line-height: 1.65em;
		text-align: center;
		margin: 10px 10px 0 0;
		float: right;
	}
	.Details-position-div-color{
		margin: 0.5em 0;
		font-size: 13px;
	}
	.Details-div-divsize{
	width: 100%;
    color: #6B6B6B;
    font-size: 0.8em;
    border-top: 1px dotted #afafaf;
    padding-top: 1em;
    padding-bottom: 0.5em;
	}
	.Details-div-divsize-daxiao{
	width: 30%;
    margin: 0.5em 1%;
    background: #F3F3F3;
    color: #606060;
    text-align: center;
    border: 1px solid #D4D3D3;
    border-radius: 0.5em;
    height: .43rem;
    overflow: hidden;
    font-size: 14px;
    line-height:.43rem ;
    text-align: center;
	}
	.Details-div-divsize-daxiao2{
	width: 30%;
    margin: 0.5em 1%;
    background: #F3F3F3;
    color: #606060;
    text-align: center;
    border: 1px solid #D4D3D3;
    border-radius: 0.5em;
    height: .43rem;
    overflow: hidden;
    font-size: 14px;
    line-height:.43rem ;
    text-align: center;
    display: none;	
	}
	.Details-position-div-color-color{
		display: flex;
		& div{
			width: 30%;
		    margin: 0.5em 1%;
		    background: #f3f3f3;
		    text-align: center;
		    border: 1px solid #D4D3D3;
		    border-radius: 0.5em;
		    height: 2em;
		    overflow: hidden;
		    font-size: 14px;
		    line-height: 2em;
		}
	}
	.Detailspositiondivcolorcolorbackground{
		background: #FF0000 !important;
		color: #fff;
	}
	.Details-cloth{
		position: absolute;
		width: 100%;
		height: 100%;
		display: block;
	}
	.Details-position-div-number1{
	background: #D7D7D7;
    float: left;
    padding: 0.4em 0.8em;
    border: 1px solid #AFAFAF;
    border-radius: 0.5em 0 0 0.5em;
    display: inline-block;
	}
	.Details-position-div-number2{
	float: left;
    padding: 0.4em 0.8em;
    border: 1px solid #AFAFAF;
    display: inline-block;
    border-left: 0;
    border-right: 0;
	}
	.Details-position-div-number3{
	background: #D7D7D7;
    float: left;
    padding: 0.4em 0.8em;
    border: 1px solid #AFAFAF;
    border-radius: 0 0.5em 0.5em 0;
    display: inline-block;
    }
    .Details-determine{
    width: 100%;
    margin: 0 auto;
    padding: 0.85em 0em;
    background: red;
    color: #fff;
    text-align: center;
    border-radius: 0em;
    position: fixed;
    bottom: 0;
    font-size: 16px;
    }
    .Details-lei{
	display: flex;flex-wrap: wrap;position: relative;
    }
    .listout{
    	animation: listout 0.5s forwards;
    }
    @keyframes listout{
    	from{    transform: translateY(0rem);}
    	to{    transform: translateY(8.5rem);}
    }
    .listyes{
    	animation: listyes 0.5s forwards;
    }
    @keyframes listyes{
    	from{    transform: translateY(8.5rem);}
    	to{    transform: translateY(0rem);}
    }
    .Details-Store{
    margin-top: 0.5em;
    padding: 0.8em 0.5em;
    background: #fff;
    font-size: 0.9em;
    position: relative;
    width: 96%;
    }
    .Details-Store-div{margin-top: 0em;padding: 0.6em 0.4em 0em 0.3em;font-size: 0.75em;display: flex;
    & div{
    	padding-top: 0.084rem;
    	padding-left: 0.084rem;
    	}
    }
    .Details-evaluation-div1{
    line-height: 2.5em;
    background: #fff;
    border-bottom: 1px solid #f3f3f3;
    position: relative;
    font-size: 0.8em;
    text-align: center;
    }
    .Details-evaluation-div2{
    	 background: #fff;
    	 padding-left: 0.1rem;
    	 font-size: 0.8em;
    }
    .Details-view{
    text-align: center;
    font-size: 1em;
    color: #d4241a;
    padding: 0.5em;
    background: #F8F8F8;
    }
    .Details-color-div{
    	width: 2rem;
    }
</style>


