<template>
  <div>
  	<div  class="cart-div1" >
  		<div class="cart-div2">  			
  		</div>
  		<div style="margin-top: 1em;color: #818382;text-align: center;">您的购物车还是空的</div>
  		<div class="cart-div-a"><router-link to="/" style=" color: #fff;" class="cart-div-a-a">随便逛逛</router-link></div>
  	</div>
  	<div class="cart-div3" style="display: none;">
  		<div class="cart-div3-div"v-for="obj in listjson">
  			<div style="display: flex;">
	  			<div class="cart-div3-yes">
	  				<input type="checkbox"  value="" />
	  			</div>
	  			<div style="display: flex;">
	  				<img :src="obj.img" class="cart-div-img"/>
	  				<div  class="cart-div-img-name">
		  				<div>{{obj.name}}</div><br />
		  				<div style="color:#ff0000;">{{obj.money}}&nbsp;&nbsp;</div>
		  				<div>{{obj.text}}</div>	  				
		  				<div class="listjson-size" style="float: right;">x{{obj.size}}</div>
	  				</div>
	  			</div>	  				  			
	  		</div>
	  			<div style="width: 100%;border-top-color: rgb(175, 175, 175);padding-top: 1em;padding-bottom: 1.5em;font-size: 16px;">
						<div class="Details-position-div-number1" @touchend="jsnumber1" style="padding: 0.1em 0.8em;">-</div>
						<div class="Details-position-div-number2" style="padding: 0.1em 0.8em;">{{number2}}</div>
						<div class="Details-position-div-number3" @touchend="jsnumber2" style="padding: 0.1em 0.8em;">+</div>
						<div class="cart-number2">
							<div @touchend="edit">编辑  |</div>
							<div @touchend="end">删除</div>
						</div>
					</div>
  		</div>
  		
  	</div>
  </div>
</template>

<script lang="babel">
import bus from "../assets/js/bus.js";
export default {
	props:{
		
	},
	data(){//数据
		return{
			number2:1,
			listjson:Array
		}	
	},
	methods:{//js
		jsnumber1(){
		   		if(this.number2>1){
		   			this.number2--;
		   		}
		   	},
		   	jsnumber2(){
		   		if(this.number2<999){
		   			this.number2++;
		   			$(".listjson-size").text(this.number2)
		   		}
		   	},
		   	edit(){
		   		$(".Details-position-div").removeClass("listout");
				$(".Details-position").css({display:"block"});
				$(".Details-position-div").toggleClass("listyes");
				bus.$emit("bool",true);
		   	},
		   	end(){
		   		$(".cart-div3-div").remove();
		   		$(".cart-div1").css({display:"block"})
		   	}
	},
	components:{//注册模块
	},
	beforeUpdate(){
	},
	updated(){
		if(this.listjson.text!=""){
			$(".cart-div1").css({display:"none"});
			$(".cart-div3").append()
		}
		else{
			$(".cart-div1").css({display:"block"})
		}
		if(this.listjson=='function Array() { [native code] }'==true){
				$(".cart-div1").css({display:"block"});
				$(".cart-div3").css({display:"none"});
			}
		else{
			$(".cart-div3").css({display:"block"});
			$(".cart-div1").css({display:"none"});
		}
	},
	beforeCreate(){

	},
	mounted(){//得到数据的ajax
			var _this=this;
//		console.log(_this.listjson)
			bus.$on("bool2",function(msg){//得到购物组件bool
				_this.listjson=msg;
			})
			
			
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
.cart-div2{
	  background: url(//img.sanfu.com/img/index_boy/cart.png) center 5em no-repeat;
    background-size: 5.5em;
    text-align: center;
    width: 10em;
    margin: 0 auto;
    height: 11em;
}
.cart-div-a{
    height: 2em;
    line-height: 2em;  
    background: #F51B1B;
    display: block;
    padding: 0em 1em;
    border: 0em solid #aaa;
    border-radius: 0em;
    width: 8em;
    margin: 1em auto;
    TEXT-DECORATION: none;
}
.cart-div-a-a{
	display: block;
	text-align: center;
}
.cart-div1{
	width: 100%;
	height: 100%;
	position: relative;
}
.cart-div3-div{
	  border-radius: 0em;
    border-top: dotted 0px #878787;
    padding: 0.5em;
    margin-bottom: 0.5em;
    background: #fff;
}
.cart-div3-yes{
    margin-top: 0.5em;
    width: 1.8em;
    background-size: 1.8em;
}
.cart-div3-yes-input{
	    position: absolute;
    top: 4px;
    right: 20px;
    display: inline-block;
    width: 28px;
    height: 26px;
    border: 0;
    outline: 0!important;
    background-color: transparent;
    -webkit-appearance: none;
}
.cart-div-img{
	width: 3.5em;
    border: 1px solid #ccc;
    margin: 0 0.5em;
    border-radius: 0.5em;
}
.Details-position-div-number1{
	background: #D7D7D7;
    float: left;
    padding: 0.4em 0.8em;
    border: 1px solid #AFAFAF;
    border-radius: 0.5em 0 0 0.5em;
    display: inline-block;
	}
	.Details-position-div-number2{
	float: left;
    padding: 0.4em 0.8em;
    border: 1px solid #AFAFAF;
    display: inline-block;
    border-left: 0;
    border-right: 0;
	}
	.Details-position-div-number3{
	background: #D7D7D7;
    float: left;
    padding: 0.4em 0.8em;
    border: 1px solid #AFAFAF;
    border-radius: 0 0.5em 0.5em 0;
    display: inline-block;
    }
    .cart-div3{
    	font-size: 0.24rem;
    	text-align: center;
    }
    .cart-div-img-name{
    	width: 4.9rem;
    	& div{
    		float: left;
    	}
    }
    .cart-number2{
    	float: right;
    	display: flex;
    	color: #ccc;
    }
</style>


