<template>
	<div>
		<div class="Detailshead-list">
			<div class="Detailshead-list-div1">
				<div><i class="fa fa-user-o"></i><div class="Detailsheadsize">客服</div></div>
				<div><i class="fa fa-heart-o"></i><div class="Detailsheadsize">收藏</div></div>
				<div @touchend="shopping"><i class="fa fa-shopping-cart"></i><div class="Detailsheadsize">购物车</div></div>
			</div>
			<div class="Detailshead-list-div2">
				<div style="background: #ff9e31;" @touchend="faangleup2">加入购物车</div>
				<div style="background: #da2105;" @touchend="faangleup2">立即购买</div>
			</div>
		</div>
	</div>
</template>

<script lang="babel">
	import bus from "../../assets/js/bus.js";
	export default {
		data() { //数据
			return {
			
			}
		},
		methods: { //js
			faangleup2() {
				$(".Details-position-div").removeClass("listout");
				$(".Details-position").css({display:"block"});
				$(".Details-position-div").toggleClass("listyes");
				bus.$emit("bool",true);
			},
			shopping(){
				$(".cart").css({display:"block"});
				$(".Details-div1").css({display:'none'});
				$(".Details-div2").css({display:'none'});
				$(".Details-div3").css({display:'none'});
				$(".Details-div4").css({display:'none'});
				$(".Details-div5").css({display:'none'});
				$(".Details").removeClass("Details");
			}
		},
		updated() {
			
			
		},
		components: { //注册模块
		},
		mounted() { //得到数据的ajax
			

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.Detailshead-list{
	background: #fff;
    text-align: center;
    height: 3em;
    line-height: 3em;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 9;
    display: flex;
    }
    .Detailshead-list-div1{
    	display: flex;
    	margin-right: .03rem;
    	& div{
    	padding-top: .15rem;
    	margin-left: .1rem;
    	width: .78rem;
    	line-height: 0;
    	}
    	height: 100%;
    }
    .Detailsheadsize{
    	font-size: 12px;
    	width: 100%;
    	margin-top: .1rem ;
    	margin-left: 0.01rem !important;
    }
    .Detailshead-list-div2{
    width: 100%;display: flex;
	    & div{
	    	font-size: 13px;
	    	color: #fff;
	    	padding: 0.03rem  0.5rem 0 .45rem;
	    	width: 50%;
	    	text-align: center;
	    }
	    & a{
	    	color: #fff;
	    }
    }
</style>


