<template>
  <div>
			<div class="Detailsimg-div1">为您优选</div>
			<Switchthebar2 :listArr="Details2"></Switchthebar2>
			<div class="Detailsimg-div2">图片详情</div>
			<div v-for="obj in listimg"><img :src="obj.imgUrl"/></div>
			<div style="padding: 0.43rem;display: block;">			
			</div>
  </div>
</template>
<script lang="babel">
import Switchthebar2 from "./Switchthebar2";
export default {
	props: {//从父得到数据
		listimg:Array
		},
	data(){//数据
		return{	
			Details2:[]
		}	
	},
	methods:{//js
		
	},
	components:{//注册模块
		Switchthebar2
	},
	updated() {
	
	},
	mounted(){//得到数据的ajax
				var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Details2.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Details2 = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
	.Detailsimg-div1{
		 margin-top: 0em;
    padding: 0.5em 0.5em;
    background: #FFF;
    color: #6C6B6A;
    font-size: 0.9em;
    border-left: #ff0000 solid 4px;
	}
	.Detailsimg-div2{
		 margin-top: 0.3em;
    padding: 0.5em 0.5em;
    background: #FFF;
    color: #6C6B6A;
    font-size: 0.9em;
    border-left: #ff0000 solid 4px;
    margin-bottom: 0.1em;
	}
</style>


