<template>
  <div class="Details">
  	<div class="Details-div4">
  	<Detailshead></Detailshead>
  	</div>
  	<div class="cart">
			<cart></cart>
		</div>
  	<div  class="Details-div1" >
	  	<list :listArr="Details1"></list>
	  	<size :lisize="Text" :lisize2="Text2" :lisize3="Text3"></size>
	  	<Detailsimg :listimg="Details3"></Detailsimg>
  	</div>
  	<div  class="Details-div2" style="display: none;">
  		<Detailspage :listpage="Details3" :listpageto="Text2"></Detailspage>
  	</div>
  	<div class="Details-div3">
  		<Commentdetails :listcommen="Commentdetails"></Commentdetails>
  	</div>
  	<cationfixed></cationfixed>
  	<div class="Details-div5">
  	<Detaillist></Detaillist>
  	</div>
  </div>
</template>
<script lang="babel">
import Detailshead from "./common/Detailshead";
import Detaillist from "./common/Detaillist";
import list from "./common/Switchthebar";
import size from "./common/Detaisize";
import Detailsimg from "./common/Detailsimg";
import Detailspage from "./common/Detailspage";
import cationfixed from "./common/cationfixed";
import Commentdetails from "./common/Commentdetails";
import cart from "./cart";
export default {
	data(){//数据
		return{
			Details1:[],
			youth:[],
			Text:{},
			Text2:{},
			Text3:{},
			Details3:[],
			Commentdetails:[]
		}	
	},
	methods:{//js
		
	},
	components:{//注册模块
	Detailshead,
	Detaillist,
	list,
	size,
	Detailsimg,
	Detailspage,
	cationfixed,
	Commentdetails,
	cart
	},
	updated() {
		this.Text=this.youth[6]
		this.Text2=this.Details1[5]
		this.Text3=this.Details1[0]
	},
	beforeMount(){
		var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/youth.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.youth = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	},
	mounted(){//得到数据的ajax
		var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Details7-1.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Details1 = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Details9.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Details3 = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Commentdetails.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Commentdetails = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
.Details{
padding-top: 0.7rem;
}
.Details-div1{
	display: block;
}
.Details-div2{
	display: none;
}
.Details-div3{
	display: none;
}
.Details-div4{
	display: block;
}
.Details-div5{
	display: block;
}
.cart{
	display: none;
}
</style>


